body {
	font-family: sans-serif;
	margin: 0;
	height: 100%;
	background-image: url("src/Resources/Images/BgSaturn.jpg");
	background-size: cover;
	background-position: center;
}

#app,
#canvas-holder {
	height: 100%;
	width: 100%;
	position: absolute;
	display: block;
}

#canvas-wrapper {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

#canvas {
	max-width: 100%;
	max-height: 100%;
}


